 
import React, { Component, useState } from "react";
import { Stepper, Step, StepLabel, Button, Box } from '@mui/material';
import Rating from '../inc/rating';
import StepOne from '../inc/stepone';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link'

const steps = ['Step 1', 'Step 2', 'Step 3', 'Step 4'];

const MultiStepForm = () => {
  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
  

  const Btns = () => {
    return (
        <div>
        <p>{steps[activeStep]}</p>
        <Button disabled={activeStep === 0} onClick={handleBack}>
          Back
        </Button>
        <Button variant="contained" color="primary" onClick={handleNext}>
          {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
        </Button>
      </div>
    );
  }
  
  var sqltext = ` 
  SET SQL_MODE = "NO_AUTO_VALUE_ON_ZERO";
  START TRANSACTION;
  SET time_zone = "+00:00";
  
  
  /*!40101 SET @OLD_CHARACTER_SET_CLIENT=@@CHARACTER_SET_CLIENT */;
  /*!40101 SET @OLD_CHARACTER_SET_RESULTS=@@CHARACTER_SET_RESULTS */;
  /*!40101 SET @OLD_COLLATION_CONNECTION=@@COLLATION_CONNECTION */;
  /*!40101 SET NAMES utf8mb4 */;
  
  CREATE TABLE 'reports' (
    'id' int(11) NOT NULL,
    'owner' text NOT NULL,
    'text' text NOT NULL,
    'pid' int(11) NOT NULL DEFAULT current_timestamp(),
    'datetime' date NOT NULL DEFAULT current_timestamp(),
    'rname' text NOT NULL,
    'identifier' text NOT NULL,
    'rip' int(11) NOT NULL,
    'uniqueid' bigint(20) NOT NULL,
    'img' text NOT NULL,
    'extends' text NOT NULL DEFAULT '[   "status": "#ff004c"  ]'
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8mb4;
  
  CREATE TABLE 'reports_banlist' (
    'id' int(11) NOT NULL,
    'identifiers' text NOT NULL
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8mb4;
  
  CREATE TABLE 'reports_players' (
    'id' int(11) NOT NULL,
    'identifier' text NOT NULL,
    'points' int(11) NOT NULL
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8mb4;
  
  ALTER TABLE 'reports'
    ADD PRIMARY KEY ('id');
  
  ALTER TABLE 'reports_banlist'
    ADD PRIMARY KEY ('id');
  
  ALTER TABLE 'reports_players'
    ADD PRIMARY KEY ('id');
  
  ALTER TABLE 'reports'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  
  ALTER TABLE 'reports_banlist'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  
  ALTER TABLE 'reports_players'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  COMMIT;
  
  /*!40101 SET CHARACTER_SET_CLIENT=@OLD_CHARACTER_SET_CLIENT */;
  /*!40101 SET CHARACTER_SET_RESULTS=@OLD_CHARACTER_SET_RESULTS */;
  /*!40101 SET COLLATION_CONNECTION=@OLD_COLLATION_CONNECTION */;
  
   
  `;

   
  sqltext = sqltext.replaceAll("'", "`");
 
  return (
    <div>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
         
        ))}
      </Stepper>
      <div className="second-center">
        {activeStep === steps.length ? (
             <Box
             sx={{
                m: 5,       
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center',
             }}
           >
          <div>
            <h3 style={{ color: '#1976d2'}}>Congratulations you have completed all the steps. You can start using your script.</h3>
            <Rating />
          </div>
          </Box>
        ) : activeStep === 0  ? (

            <div>
                <StepOne />
                <Btns />
            </div>
           
          ) : activeStep === 1  ? (
            <div>
                  <h1>Import the sql </h1>
                <TextField
          id="outlined-multiline-static3"
          label="Import the sql "
          multiline
          fullWidth 
          rows={50}
          value={sqltext}
        />
        
        <Btns />
            </div>
        ) : activeStep === 2  ? (
            <Box sx={{ '& button ': { m: 1 }  }}>
                <h1>Required Scripts will be need</h1>

                <Link href="https://github.com/citizenfx/screenshot-basic"><Button variant="outlined">screenshot-basic</Button></Link>
                <Link href="https://github.com/0resmon/yarn"><Button variant="outlined">yarn</Button></Link>
                <Link href="https://github.com/0resmon/webpack"><Button variant="outlined">webpack</Button></Link>
                <Link href="#"><Button variant="outlined">s4-render</Button></Link>
                <div style={{ marginTop: "2vh" }}>
                Get s4-render <Link href="https://discord.gg/0resmon">https://discord.gg/0resmon</Link> to download it from.
                </div>
                

             <Btns />
            </Box>
        ) : activeStep === 3 ? (
          <Box sx={{ '& button ': { m: 1 }  }}>
          <h1>Start Resources</h1>
          <p>Put the script in your source file and run it from your server.cfg file.</p>
          <TextField
          id="outlined-multiline-static3"
          label="Import the sql "
          multiline
          fullWidth 
          rows={3}
          value={`ensure s4-reportsystem\nensure s4-render`}
        />
            <Btns />
        </Box>
        ) : (
            <Btns />
        )}
      </div>
    </div>
  );
};


class ReportV1 extends Component {
 
  

  render() {
   return (
    <div className="Second" >
        <header className="second-header">
            <h1>Report System v1</h1>
        </header>
        <MultiStepForm />
    </div>  
    );
  }
}
 
export default ReportV1; 