import React from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';

const Footer = () => {
  return (
    <Box
      component="footer"
      sx={{
        py: 25,
        textAlign: 'center',
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        typography: 'body2',
        '& > :not(style) ~ :not(style)': {
          ml: 2,
        },
      }}
    >
      
    <Link href="https://0resmon.tebex.io" underline="none">0resmon.tebex.io</Link>
    <Link href="https://forum.cfx.re/u/s4leh/" underline="none">forum.cfx.re/u/s4leh/</Link>
    <Link href="mailto:salih@cokluk.com" underline="none">salih@cokluk.com</Link>

    </Box>
  );
};

export default Footer;