 
import React, { Component } from "react";
import Card from '@mui/material/Card';
import Box from '@mui/material/Box';
import { Link } from 'react-router-dom';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import Scripts from '../data/scripts.json';
import Button from '@mui/material/Button';
import CardActions from '@mui/material/CardActions';


class HowToInstall extends Component {
 
  scripts = Scripts;

  render() {
   return (
    <div className="Second" >
    <header className="second-header">
        <h1>How To Install</h1>
        <p>Select your script and follow the installation steps.</p>
    </header>
     

    <Box sx={{
        m: 5,       
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
      }}
      >
        {this.scripts.map((v) => (
          <Link to={v.to} style={{textDecoration: 'none' }}>
          <Card sx={{ maxWidth: 345, margin: 1 }}  >
            <CardActionArea>
              <CardMedia
                component="img"
                height="140"
                image={v.url}
                alt="green iguana"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  {v.name}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                Learn how to install script and configure config settings.
                </Typography>
              </CardContent>
              <CardActions>
                <Button href={v.buyUrl} size="small">{v.price === "free" ? (
                    "Download" 
                ) : (
                    "Buy " + v.price
                )}</Button>
              </CardActions>
            </CardActionArea>
          </Card>
          </Link>
        ))}
     </Box>


    
  </div>  
 
    );
  }
}
 
export default HowToInstall; 