 
import React, { Component, useState } from "react";
import { Stepper, Step, StepLabel, Button, Box } from '@mui/material';
import Rating from '../inc/rating';
import StepOne from '../inc/stepone';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link'

const steps = ['Step 1', 'Step 2', 'Step 3', 'Step 4'];

const MultiStepForm = () => {
  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
  

  const Btns = () => {
    return (
        <div>
        <p>{steps[activeStep]}</p>
        <Button disabled={activeStep === 0} onClick={handleBack}>
          Back
        </Button>
        <Button variant="contained" color="primary" onClick={handleNext}>
          {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
        </Button>
      </div>
    );
  }
  
  var sqltext = ` 
        
  CREATE TABLE 'reports' (
    'id' int(11) NOT NULL,
    'owner' text NOT NULL,
    'text' text NOT NULL,
    'pid' int(11) NOT NULL DEFAULT current_timestamp(),
    'datetime' date NOT NULL DEFAULT current_timestamp(),
    'rname' text NOT NULL,
    'identifier' text NOT NULL,
    'rip' int(11) NOT NULL,
    'uniqueid' bigint(20) NOT NULL,
    'img' text NOT NULL,
    'extends' text NOT NULL DEFAULT '{   "status": "#ff004c" }'
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8mb4;
  
  CREATE TABLE 'reports_banlist' (
    'id' int(11) NOT NULL,
    'identifiers' text NOT NULL
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8mb4;
  
  
  CREATE TABLE 'reports_players' (
    'id' int(11) NOT NULL,
    'identifier' text NOT NULL,
    'points' int(11) NOT NULL
  ) ENGINE=InnoDB DEFAULT CHARSET=utf8mb4;
  
  
  CREATE TABLE 's4-hooks' (
    'pr' varchar(50) NOT NULL,
    'data' longtext DEFAULT NULL
  );
  
  ALTER TABLE 'reports'
    ADD PRIMARY KEY ('id');
  
  ALTER TABLE 'reports_banlist'
    ADD PRIMARY KEY ('id');
  
  ALTER TABLE 'reports_players'
    ADD PRIMARY KEY ('id');
  
  
  ALTER TABLE 'reports'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  
  ALTER TABLE 'reports_banlist'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  
  ALTER TABLE 'reports_players'
    MODIFY 'id' int(11) NOT NULL AUTO_INCREMENT;
  COMMIT;
  
  
  
   
  `;

   
  sqltext = sqltext.replaceAll("'", "`");
 
  return (
    <div>
      <Stepper activeStep={activeStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
         
        ))}
      </Stepper>
      <div className="second-center">
        {activeStep === steps.length ? (
             <Box
             sx={{
                m: 5,       
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center',
             }}
           >
          <div>
            <h3 style={{ color: '#1976d2'}}>Congratulations you have completed all the steps. You can start using your script.</h3>
            <Rating />
          </div>
          </Box>
        ) : activeStep === 0  ? (

            <div>
                <StepOne />
                <Btns />
            </div>
           
        ) : activeStep === 1  ? (
            <div>
                <h1>Change webhooks from "s4-render/config.lua" </h1>
                <TextField
          id="outlined-multiline-static"
          label="Change webhook"
          multiline
          fullWidth 
          rows={8}
          value={` 
          Config = {}
          Config.ScreenshotWebhook = "CHANGE THIS WEBHOOK"
          Config.VideoWebhook = "CHANGE THIS WEBHOOK"
          `}
        />
        <h1>How to create webhook from discord</h1>
        <iframe width="560" height="315" src="https://www.youtube.com/embed/fKksxz2Gdnc" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
        <h1>Change webhooks from "s4-reportsystemv2/config.lua" </h1>
        <TextField
          id="outlined-multiline-static1"
          label="Change webhook"
          multiline
          fullWidth 
          rows={8}
          value={` 
            Config = {}

            Config.ReportCommand = "rep"

            Config.Webhook = "CHANGE THIS WEBHOOK"
          `}
        />
        <h2>And configure the config according to your server. </h2>

        <h1>Change webhooks from "s4-reportsystemv2/sv_config.lua" </h1>
      
        <TextField
          id="outlined-multiline-static2"
          label="Edit auth"
          multiline
          fullWidth 
          rows={8}
          value={` 
            ServerCfg = {}
            ServerCfg.AutoImportSql = false -- mysql server must support it!
            ServerCfg.Identifiers = { "INSERT YOUR IDENTIFIER", "2ND IDENTIFIER (OPTIONAL)", "3ND IDENTIFIER (OPTIONAL)"  }
            ServerCfg.AdminGroups =  { "admin", "user" }
          `}
        />
          <h2>Do not recognize your identifier to use admin commands. </h2>
          <h3 style={{color: 'red'}}>Please do not copy and paste the config. Only edit the files you have. </h3>
                <Btns />
            </div>
         ) : activeStep === 2  ? (
            <div>
                  <h1>Import the sql </h1>
                <TextField
          id="outlined-multiline-static3"
          label="Import the sql "
          multiline
          fullWidth 
          rows={50}
          value={sqltext}
        />
        
        <Btns />
            </div>
        ) : activeStep === 3  ? (
            <div>
                <h1>Ensure scripts</h1>
                <h2>Do not forget to add these commands to your server.cfg file.</h2>
                <TextField
          id="outlined-multiline-static3"
          label="Import the sql "
          multiline
          fullWidth 
          rows={5}
          value={`
            ensure MugShotBase64
            ensure s4-render
            ensure s4-reportsystemv2
          `}
        />

        <h3><Link target='_blank' href="https://github.com/BaziForYou/MugShotBase64">MugShotBase64</Link> Scripts is an excerpt script. Thanks to the script maker BaziForYou.  </h3>
            <Btns />
            </div>
        ) : (
            <Btns />
        )}
      </div>
    </div>
  );
};


class ReportV2 extends Component {
 
  

  render() {
   return (
    <div className="Second" >
        <header className="second-header">
            <h1>Report System v2</h1>
        </header>
        <MultiStepForm />
    </div>  
    );
  }
}
 
export default ReportV2; 