import React from 'react';
import Box from '@mui/material/Box';
import one from '../images/1.png';
import two from '../images/2.png';

const StepOne = () => {
  return (
    <Box>
        <h1>Click to Granted Assets</h1>
        <img
        src={one}
        alt="keymaster granted assets"
        loading="lazy"
      />
       <h1>And download to asset</h1>
      <img
        src={two}
        alt="keymaster granted assets"
        loading="lazy"
      />
      

    </Box>
  );
};

export default StepOne;